.OfflineEditor-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  max-width: 800px;
  min-height: 100%;
  padding: 20px 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: white;
  margin-left: auto;
  margin-right: auto;
}

.OfflineEditor-container h1,
.OfflineEditor-container .DraftEditor-root {
  color: black;
}

.OfflineEditor-container h1 {
  letter-spacing: -1px;
}

.OfflineEditor-container .DraftEditor-root {
  min-height: 150px;
  margin: 0 -20px;
  padding: 0 20px;
  line-height: 30px;
  background: -webkit-repeating-linear-gradient(
    bottom,
    #eee,
    #eee 1px,
    white 1px,
    white 30px
  );
  background: -o-repeating-linear-gradient(
    bottom,
    #eee,
    #eee 1px,
    white 1px,
    white 30px
  );
  background: repeating-linear-gradient(
    0deg,
    #eee,
    #eee 1px,
    white 1px,
    white 30px
  );
}


